import {React, useState} from 'react'
import {CRow, CCol, CDataTable, CButton, CModalHeader, CModalBody, CModal, CFormGroup, CLabel, CInput, CModalFooter, CBadge} from '@coreui/react'
import {ModalBarang} from './bridge'
import Select from 'react-select'
import DataKemasan from './datakemasan'

const Barang = () =>{
    const [modal, setModal] = useState(false)
    const [pilihan, setPilihan] = useState(1)
    const [modalkontainer, setModalKontainer] = useState(false)
    return(
        <>
        <CModal
        show={modalkontainer}
        size="xl"
        onClose={()=>setModalKontainer(false)}
        >
            <CModalHeader><h5>Tambah Kontainer</h5></CModalHeader>
            <CModalBody>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Kontainer</CLabel>
                                <CInput placeholder="Nomor Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Ukuran Kontainer</CLabel>
                                <Select
                                />
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Status / Stuffing Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'FCL', value:1},
                                    {label:'LCL', value:2}
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Jumlah Kontainer</CLabel>
                                <CInput placeholder="Jumlah Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Seal</CLabel>
                                <CInput placeholder="Nomor Seal"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Tipe Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'General/Dry Cargo Container', value:1},
                                    {label:'Tunnel Type Container', value:2},
                                    {label:'Open Top Steel Container', value:3},
                                    {label:'Flat Rack Container', value:4},
                                    {label:'Reefer/Refrigerated Container', value:5},
                                    {label:'Barge Container', value:6},
                                    {label:'Bulk Container', value:7},                                                                        
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>                                        
                </div>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row">
                    <div className="mr-2">
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-success">Simpan</CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-danger">Batal</CButton>
                    </div>                    
                </div>
            </CModalFooter>
        </CModal>
        <ModalBarang modal={modal} callback={(x)=>{
            setModal(x)
        }}/>
        <div className="d-flex flex-row">
            <div className={
                pilihan == 1 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(1)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Barang
            </div>
            {/* <div className={
                pilihan == 2 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(2)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kontainer
            </div>
            <div className={
                pilihan == 3 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(3)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kemasan
            </div>                         */}
        </div>
        {
            pilihan == 1 ?
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-end mb-2">
                            <CButton onClick={()=>setModal(true)} className="btn btm-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-plus"></i></CButton>
                        </div>
                        <CDataTable
                        addTableClasses="joss"
                        fields={[
                            {label:'No', key:'nomor'},
                            {label:'Kode HS', key:'hscode'},
                            {label:'Uraian', key:'uraian'},
                            {label:'Negara Asal', key:'negAsal'},
                            {label:'Karantina', key:'karantina'},
                            {label:'Action', key:'action'}
                        ]}
                        items={[
                            {
                                nomor:'1',
                                hscode:'03011910',
                                uraian:'Clown Fish Marine',
                                negAsal:'ID',
                                karantina:'Ikan'
                            },
                            {
                                nomor:'2',
                                hscode:'26180000',
                                uraian:'Terak butiran (pasir terak) dari industri pembuatan besi atau baja',
                                negAsal:'ID',
                                karantina:'Tidak'
                            }                            
                        ]}
                        scopedSlots={{
                            'karantina':
                            (item, index)=>{
                                return(
                                    <td>
                                    <CBadge color={item.karantina == 'Ikan' ? 'success' : 'danger'}>
                                        {item.karantina}
                                    </CBadge>                                        
                                    </td>
                                )
                            },
                            'action':
                            (item, index)=>{
                                return(
                                    <td>
                                        <div className="d-flex flex-row justify-content-start">
                                            <div className="mr-2">
                                                <CButton className="btn btn-sm" color="success"><i class="fas fa-pencil-alt"></i></CButton>
                                            </div>                                            
                                            <div>
                                                <CButton className="btn btn-sm" color="danger"><i class="fas fa-trash"></i></CButton>
                                            </div>
                                        </div>
                                    </td>
                                )
                            }
                        }}
                        />
                    </CCol>
                </CRow>                  
            </div>          
            </>     
            :
            pilihan == 2 ?
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-end mb-2">
                            <CButton onClick={()=>setModalKontainer(true)} className="btn btm-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-plus"></i></CButton>
                        </div>
                        <CDataTable
                        addTableClasses="joss"
                        fields={[
                            {key:'no',label:'Nomor'},
                            {key:'noCont',label:'Nomor Kontainer'},
                            {key:'ukCont',label:'Ukuran Kontainer'},
                            {key:'statusCont',label:'Status Kontainer'},
                            {key:'noSeal',label:'Nomor Seal'},
                            {key:'action',label:'Action'},
                        ]}
                        items={[
                            {
                                no:'1',
                                noCont:'CN12345',
                                ukCont:'40',
                                statusCont:'FCL',
                                noSeal:'SL12345'                                
                            }
                        ]}
                        scopedSlots={{
                            'action':
                            (item, index)=>{
                                return(
                                    <td>
                                        <div className="d-flex flex-row justify-content-start">
                                            <div className="mr-2">
                                                <CButton className="btn btn-sm" color="success"><i class="fas fa-pencil-alt"></i></CButton>
                                            </div>                                            
                                            <div>
                                                <CButton className="btn btn-sm" color="danger"><i class="fas fa-trash"></i></CButton>
                                            </div>
                                        </div>
                                    </td>
                                )
                            }
                        }}
                        />
                    </CCol>
                </CRow>                 
            </div>           
            </>
            :
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <DataKemasan/>                
            </div>                        
            </>       
        }
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Nilai</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>                
                <CCol md="3">
                    <CFormGroup>
                        <CLabel>Bruto</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>
                <CCol md="1">
                    <CFormGroup>
                        <CLabel>Satuan</CLabel>
                        <CInput placeholder="KGM" disabled/>
                    </CFormGroup>
                </CCol>  
                <CCol md="3">
                    <CFormGroup>
                        <CLabel>Netto</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>
                <CCol md="1">
                    <CFormGroup>
                        <CLabel>Satuan</CLabel>
                        <CInput placeholder="KGM" disabled/>
                    </CFormGroup>
                </CCol>                                
            </CRow>                        
        </div>        
        </>
    )
}

export default Barang