import { CModal, CModalHeader, CModalBody, CModalFooter, CDataTable, CButton, CCard, CRow, CCol, CFormGroup, CLabel, CInput, CTextarea, CCollapse} from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useSelector } from 'react-redux'
import Select from 'react-select'
import DataKemasan from './datakemasan'


const Pengangkutan = () =>{
    const [transit, setTransit] = useState(false)
    const [modalkontainer, setModalKontainer] = useState(false)
    const [pilihan, setPilihan] = useState(1)
    const [pengangkut, setPengangkut] = useState({})    
    const datasupabase =  useSelector((state)=>state.datasupabase)
    useEffect(()=>{
        setPengangkut({...(datasupabase.jsondata ? datasupabase.jsondata.pengangkutan : {})})
    },[])
    const handleInputSelect = (e, index) =>{
        setPengangkut({...pengangkut,[index.name]:e})
    }
    const handleInputChange = (e) =>{
        setPengangkut({...pengangkut, [e.target.name]:e.target.value})
    }
    const handleCheck = (e) =>{
        setPengangkut({...pengangkut, [e.target.name]:e.target.checked})
    }
    return(
        <>
        <CModal
        show={modalkontainer}
        size="xl"
        onClose={()=>setModalKontainer(false)}
        >
            <CModalHeader><h5>Tambah Kontainer</h5></CModalHeader>
            <CModalBody>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Kontainer</CLabel>
                                <CInput placeholder="Nomor Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Ukuran Kontainer</CLabel>
                                <Select
                                />
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Status / Stuffing Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'FCL', value:1},
                                    {label:'LCL', value:2}
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Jumlah Kontainer</CLabel>
                                <CInput placeholder="Jumlah Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Seal</CLabel>
                                <CInput placeholder="Nomor Seal"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Tipe Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'General/Dry Cargo Container', value:1},
                                    {label:'Tunnel Type Container', value:2},
                                    {label:'Open Top Steel Container', value:3},
                                    {label:'Flat Rack Container', value:4},
                                    {label:'Reefer/Refrigerated Container', value:5},
                                    {label:'Barge Container', value:6},
                                    {label:'Bulk Container', value:7},                                                                        
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>                                        
                </div>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row">
                    <div className="mr-2">
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-success">Simpan</CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-danger">Batal</CButton>
                    </div>                    
                </div>
            </CModalFooter>
        </CModal>        
        <div className="d-flex flex-row">
            <div className={
                pilihan == 1 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(1)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Pengangkutan
            </div>
            <div className={
                pilihan == 2 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(2)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kontainer
            </div>
            <div className={
                pilihan == 3 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(3)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kemasan
            </div>                        
        </div>        
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            {
                pilihan == 1 ?
                <>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-between">
                        <h5>Sarana Pengangkut</h5>
                        <div>
                            <input value={pengangkut.fltransit} name="fltransit" onChange={(e)=>handleCheck(e)} type="checkbox"/>
                             Transit ?
                        </div>
                        </div>
                        <hr/>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Cara Pengangkutan</CLabel>
                                    <Select
                                    name="caraPengangkutan"
                                    value={pengangkut.caraPengangkutan}
                                    onChange={(e, index)=>handleInputSelect(e, index)}
                                    options={[
                                        {label:'Laut', value:'1'},
                                        {label:'Udara', value:'2'},
                                        {label:'Darat', value:'3'},
                                        {label:'Instalasi/Pipa', value:'4'},
                                        {label:'Lainnya', value:'5'},
                                    ]}
                                    />
                                </CFormGroup>
                            </CCol>                   
                            <CCol md="8">
                                <CFormGroup>
                                    <CLabel>Nama Sarana Pengangkut</CLabel>
                                    <CInput
                                    name="nmSaranaPengangkut"
                                    value={pengangkut.nmSaranaPengangkut}
                                    onChange={(e)=>handleInputChange(e)}
                                    placeholder="Nama Sarana Pengangkut"
                                    />
                                </CFormGroup>
                            </CCol>                                          
                        </CRow>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel><i>Call Sign</i></CLabel>
                                    <CInput
                                    name="callsign"
                                    value={pengangkut.callsign}
                                    onChange={(e)=>handleInputChange(e)}                            
                                    placeholder="Call Sign"
                                    />
                                </CFormGroup>
                            </CCol>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel><i>Bendera</i></CLabel>
                                    <Select
                                    name="bendera"
                                    value={pengangkut.bendera}
                                    onChange={(e, index)=>handleInputSelect(e, index)}                            
                                    />
                                </CFormGroup>
                            </CCol>  
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Nomor Voy / Flight</CLabel>
                                    <CInput
                                    name="novoyflight"
                                    value={pengangkut.novoyflight}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    placeholder="Nomor Voy / Flight"
                                    />
                                </CFormGroup>
                            </CCol>                                        
                        </CRow>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Perkiraan Tanggal Berangkat</CLabel>
                                    <CInput 
                                    name="estdept"
                                    value={pengangkut.estdept}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    placeholder="Perkiraan Tanggal Berangkat"
                                    type="date"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel><i>Perkiraan Tanggal Tiba</i></CLabel>
                                    <CInput 
                                    name="estarv"
                                    value={pengangkut.estarv}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    type="date"/>
                                </CFormGroup>
                            </CCol>                     
                        </CRow>
                    </CCol>          
                </CRow>
                <CCollapse show={pengangkut.fltransit}>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-between">
                        <h5>Sarana Pengangkut Transit</h5>
                        </div>
                        <hr/>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Cara Pengangkutan Transit</CLabel>
                                    <Select
                                    name="caraPengangkutanTransit"
                                    value={pengangkut.caraPengangkutan}
                                    onChange={(e, index)=>handleInputSelect(e, index)}                            
                                    options={[
                                        {label:'Laut', value:'1'},
                                        {label:'Udara', value:'2'},
                                        {label:'Darat', value:'3'},
                                        {label:'Instalasi/Pipa', value:'4'},
                                        {label:'Lainnya', value:'5'},
                                    ]}
                                    />
                                </CFormGroup>
                            </CCol>                   
                            <CCol md="8">
                                <CFormGroup>
                                    <CLabel>Nama Sarana Pengangkut Transit</CLabel>
                                    <CInput
                                    name="nmSaranaPengangkut"
                                    value={pengangkut.nmSaranaPengangkut}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    placeholder="Nama Sarana Pengangkut Transit"
                                    />
                                </CFormGroup>
                            </CCol>                                          
                        </CRow>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel><i>Call Sign</i> Transit</CLabel>
                                    <CInput
                                    name="callsignTransit"
                                    value={pengangkut.callsignTransit}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    placeholder="Call Sign Transit"
                                    />
                                </CFormGroup>
                            </CCol>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Bendera Transit</CLabel>
                                    <Select
                                    name="benderaTransit"
                                    value={pengangkut.benderaTransit}
                                    onChange={(e, index)=>handleInputSelect(e, index)}                              
                                    />
                                </CFormGroup>
                            </CCol>  
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Nomor Voy / Flight Transit</CLabel>
                                    <CInput
                                    name="novoyflightTransit"
                                    value={pengangkut.novoyflighttransit}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    placeholder="Nomor Voy / Flight Transit"
                                    />
                                </CFormGroup>
                            </CCol>                                        
                        </CRow>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Perkiraan Tanggal Berangkat Transit</CLabel>
                                    <CInput 
                                    name="estdeptTransit"
                                    value={pengangkut.estdeptTransit}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    type="date"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel><i>Perkiraan Tanggal Tiba Transit</i></CLabel>
                                    <CInput 
                                    name="estarv"
                                    value={pengangkut.estarvTransit}
                                    onChange={(e)=>handleInputChange(e)}                             
                                    type="date"/>
                                </CFormGroup>
                            </CCol>                     
                        </CRow>
                    </CCol>            
                </CRow>            
                </CCollapse>
                <CRow>
                    <CCol md="6">
                        <h5>Kepelabuhanan</h5>
                        <hr/>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Pelabuhan Muat Asal</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol> 
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Pelabuhan Muat Ekspor</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol>  
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Pelabuhan Transit</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol>                                                                              
                        </CRow>
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Pelabuhan Bongkar</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol>   
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Pelabuhan Tujuan</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol> 
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Tempat Penimbunan</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol>                                                                                                
                        </CRow> 
                        <CRow>
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Negara Asal</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol> 
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Negara Transit</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol> 
                            <CCol md="4">
                                <CFormGroup>
                                    <CLabel>Negara Tujuan</CLabel>
                                    <Select
                                    />
                                </CFormGroup>
                            </CCol>                                                                                                    
                        </CRow>                              
                    </CCol>
                    <CCol md="6">
                        <h5>Lokasi Pemeriksaan</h5>
                        <hr/>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Kantor Pabean Periksa</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Lokasi Pemeriksaan</CLabel>
                                    <Select
                                    options={[
                                        {label:'Dalam Bandara/Pelabuhan', value:'1'},
                                        {label:'Luar Bandara/Pelabuhan', value:'2'},
                                    ]}
                                    />
                                </CFormGroup>
                            </CCol>                    
                        </CRow>
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Alamat Pemeriksa</CLabel>
                                    <CTextarea
                                    placeholder="Alamat Pemeriksa"
                                    />
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Tanggal Periksa</CLabel>
                                    <CInput type="date"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Tanggal Tiba</CLabel>
                                    <CInput type="date"/>
                                </CFormGroup>
                            </CCol>                    
                        </CRow>                                
                    </CCol>
                </CRow>               
                </>   
                :
                pilihan == 2 ?
                <>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <CRow>
                        <CCol md="12">
                            <div className="d-flex flex-row justify-content-end mb-2">
                                <CButton onClick={()=>setModalKontainer(true)} className="btn btm-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-plus"></i></CButton>
                            </div>
                            <CDataTable
                            addTableClasses="joss"
                            fields={[
                                {key:'no',label:'Nomor'},
                                {key:'noCont',label:'Nomor Kontainer'},
                                {key:'ukCont',label:'Ukuran Kontainer'},
                                {key:'statusCont',label:'Status Kontainer'},
                                {key:'noSeal',label:'Nomor Seal'},
                                {key:'action',label:'Action'},
                            ]}
                            items={[
                                {
                                    no:'1',
                                    noCont:'CN12345',
                                    ukCont:'40',
                                    statusCont:'FCL',
                                    noSeal:'SL12345'                                
                                }
                            ]}
                            scopedSlots={{
                                'action':
                                (item, index)=>{
                                    return(
                                        <td>
                                            <div className="d-flex flex-row justify-content-start">
                                                <div className="mr-2">
                                                    <CButton className="btn btn-sm" color="success"><i class="fas fa-pencil-alt"></i></CButton>
                                                </div>                                            
                                                <div>
                                                    <CButton className="btn btn-sm" color="danger"><i class="fas fa-trash"></i></CButton>
                                                </div>
                                            </div>
                                        </td>
                                    )
                                }
                            }}
                            />
                        </CCol>
                    </CRow>                 
                </div>           
                </>
                :
                <>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <DataKemasan/>                
                </div> 
                </>                                
            }         
        </div>
        </>
    )
}

export default Pengangkutan